.hero {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to right, #6C5CE7 50%, white 50%);
    text-align: center;
    padding: 40px;
  }
  
  .hero-content {
    width: 60%;
  }
  
  .hero h1 {
    font-size: 80px;
    font-weight: 700;
    font-family: "Georgia", serif;
    color: black;
    text-transform: uppercase;
    margin-bottom: 20px;
    letter-spacing: 2px;
  }
  
  .hero p {
    font-size: 22px;
    font-family: "Georgia", serif;
    color: black;
    line-height: 1.6;
    font-weight: 400;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .explore-btn {
    margin-top: 20px;
    background: black;
    color: white;
    padding: 15px 30px;
    font-size: 22px;
    font-family: "Georgia", serif;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    transition: 0.3s ease-in-out;
  }
  
  .explore-btn:hover {
    background: #4834d4;
    color: white;
    transform: scale(1.05);
  }
  